import React, {
	FC,
} from "react";

import ButtonForBrand from "../../ButtonForBrand/ButtonForBrand";
import LogoMark from "../../Logos/LogoMark";
import { colors } from "../../styleguide";

import {
	NavItemsWrapper,
	NavList,
	NavWrapper,
	SettingsButton,
	UserMenuTooltip,
} from "./GlobalNavbar.styles";

export interface GlobalNavbarProps {
	dataQA: string
	ariaLabel: string
	className?: string
	logoPath?: string
	brandIconText?: string
	brandTooltipText?: string
	brandIconImage?: string
	brandButtonOnClick?: React.MouseEventHandler<HTMLButtonElement>
	settingsButtonOnClick?: React.MouseEventHandler<HTMLButtonElement>
	children?: React.ReactNode
}
/** @deprecated Use GlobalNavigation */
const GlobalNavbar: FC<GlobalNavbarProps> = ({
	dataQA,
	ariaLabel,
	children,
	className,
	logoPath = "/",
	brandIconText,
	brandTooltipText,
	brandIconImage,
	brandButtonOnClick,
	settingsButtonOnClick,
}) => (
	<NavWrapper
		aria-label={ariaLabel}
		data-qa={dataQA}
		className={className}
	>
		<NavItemsWrapper>
			<div>
				<NavList>
					<li>
						<a href={logoPath}>
							<LogoMark
								title="Wunderkind"
								width="40px"
								fill={colors.white}
								m="0 16px 30px 16px"
								dataQA={`${dataQA}-logo`}
							/>
						</a>
					</li>
					{children}
				</NavList>
			</div>
			<div>
				<NavList>
					<li>
						<UserMenuTooltip
							dataQA={`${dataQA}-brand-button-tooltip`}
							position="right"
							zIndex={99}
							text={brandTooltipText}
						>
							<ButtonForBrand
								dataQA={`${dataQA}-brand-button`}
								brandIconText={brandIconText}
								brandIconImage={brandIconImage}
								onClick={brandButtonOnClick}
							/>
						</UserMenuTooltip>
					</li>
					<li>
						<UserMenuTooltip
							dataQA={`${dataQA}-settings-button-tooltip`}
							position="right"
							zIndex={99}
							text="Settings"
						>
							<SettingsButton
								dataQA={`${dataQA}-settings-button`}
								icon="Settings"
								variant="primary"
								onClick={settingsButtonOnClick}
							/>
						</UserMenuTooltip>
					</li>
				</NavList>
			</div>
		</NavItemsWrapper>
	</NavWrapper>
);
GlobalNavbar.displayName = "GlobalNavbar";
export default GlobalNavbar;
